/**
 * res: HTML, responseStart --> responseEnd
 * dom: responseEnd --> domInteractive | Date.now()
 * render: new Vue() --> mounted 
 * js: index.js --> new Vue() 
 * @return {{res: string, dom: string}}
 */
export const getMoreInfo = () => {
  const timing = performance.timing

  const now = Date.now()
  // domdomInteractive0
  const domInteractive = timing.domInteractive ? timing.domInteractive : now

  // console.log(timing.domInteractive, domInteractive, timing.responseEnd)

  const res = Math.max(timing.responseEnd - timing.responseStart, 0).toFixed(0)

  // 4.4.2responseEnd 0
  const dom = Math.max(domInteractive - (timing.responseEnd || now), 0).toFixed(0)

  const more = {
    res,
    dom
  }
  return more
}
