
import util from './util'
const { getCookie, loadScript } = util

let hdid = 0
let mac = 0
let imei = 0
let Cuid = getCookie('yyuid')
let Cid = ''
let Ctype = 'friend'
let Cdebug = false

const setAppCallback = () => {
  window.getIMEICallback = function (argimei) {
    console.log(`imei-${argimei}`)
    imei = argimei
  }
  window.getHDIDCallback = function (arghdid) {
    console.log(`hdid-${arghdid}`)
    hdid = arghdid
  }
  window.getMacCallback = function (argmac) {
    console.log(`mac-${argmac}`)
    mac = argmac
  }
}

const queryDeviceInfo = function () {
  const client = window.nativeApp
  if (client && client.getIMEI && client.getHDID && client.getMac) {
    client.getIMEI()
    client.getHDID()
    client.getMac()
  } else {
    console.log('/# kxd-report #/ app IMEIHDIDMac')
  }
}

const report = (opt) => {
  !Cuid && (Cuid = getCookie('yyuid'))
  const hevent = new hiidoEvent(opt.type || Ctype, opt.id || Cid)  // eslint-disable-line
  hevent.setUid(opt.uid || Cuid)
  hevent.setImei(imei)
  hevent.setMac(mac)
  hevent.setHdid(hdid)
  hevent.setMoreinfo(opt.data)
  hevent.reportJudge()
}

setAppCallback()
queryDeviceInfo()

export default function (opt) {
  if (window.hiidoEvent) {
    if (Cdebug) {
      console.log('/# kxd-report #/ hiidoEventwebsdk')
      console.log('/# kxd-report #/  ', `id: ${opt.id || Cid}`, `uid: ${opt.uid || Cuid}`, '', opt.data)
    }
    report(opt)
  } else {
    console.log('/# kxd-report #/ hiidoEventwebsdk')
    loadScript('//cdn.hiido.com/sdk/websdk.js', () => {
      console.log('/# kxd-report #/ websdk')
      report(opt)
    })
  }
}

export function KxdReportPreset (opt) {
  const { uid, id, type, debug } = opt
  if (uid) {
    Cuid = uid
  }
  if (id) {
    Cid = id
  }
  if (type) {
    Ctype = type
  }
  if (debug) {
    Cdebug = debug
  }
}
